.extra {
	padding: 40px 0 50px;
	background-color: #f5f8fc;
	@include lg {
		padding: 25px 0 30px; }
	&__container {
		@include lg {
			max-width: 1400px; }
		@include lg {
			max-width: 1200px; } }
	&__title {
		text-align: center;
		@include lg {
			font-size: 20px; }
		@include md {
			font-size: 18px; }
		@include xs {
			font-size: 16px; } }
	&__inn {
		margin-top: 45px;
		font-size: 0;
		@include lg {
			margin-top: 30px; }
		@include xs {
			margin-top: 20px; } }
	&__text {
		display: inline-block;
		vertical-align: top;
		width: (555/1680 * 100%);
		@include md {
			vertical-align: middle; }
		@include sm {
			width: 50%; }
		@include xs {
			width: 48%; } }
	&__item {
		& + & {
			margin-top: 55px;
			@include lg {
				margin-top: 35px; }
			@include md {
				margin-top: 25px; } }
		&-list {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			position: relative;
			padding-left: 120px;
			@include lg {
				padding-left: 90px; }
			@include lg {
				padding-left: 50px; } }
		&-icon, &-icon_parts::before {
			position: absolute;
			left: 0;
			top: 50%;
			z-index: 1;
			transform: translateY(-50%);
			fill: #B8BEC4;
			@include md {
				transform: translateY(-53%) scale(.7);
				left: -5px; } }
		&-icon_parts {
			content: '';
			@include s('parts'); }
		.icon-polygraph {
			font-size: 44px; }
		.icon-framing {
			font-size: 83px; }
		&-title {
			padding-left: 120px;
			margin-bottom: 20px;
			@include f("psb",19,26);
			@include lg {
				padding-left: 90px;
				font-size: 17px; }
			@include md {
				font-size: 15px;
				padding-left: 50px; }
			@include xs {
				margin-bottom: 10px;
				font-size: 14px; } }
		&-product {
			margin-bottom: 3px;
			font-size: 19px;
			@include lg {
				font-size: 17px; }
			@include md {
				font-size: 15px; }
			@include xs {
				font-size: 14px; } } }
	&__link {
		font-size: 20px;
		@include lg {
			font-size: 18px; }
		@include md {
			font-size: 16px; }
		@include sm {
			font-size: 14px; } }
	&__links {
		margin-top: 20px;
		padding-left: 120px;
		@include lg {
			padding-left: 50px; } }
	&__pic {
		position: relative;
		display: inline-block;
		vertical-align: top;
		width: (930/1680 * 100%);
		@include md {
			vertical-align: middle; }
		@include sm {
			width: 50%; }
		@include xs {
			width: 52%; }
		&::after {
			content: '';
			display: block;
			padding-top: 70.8%; } }
	&__img {
		@include coverdiv;
		@include cover; } }
